import semver from 'semver';
import DocsSource from './DocsSource';

const branchBlacklistRegex = /(docs|(dependabot\/*))/i;
export default new DocsSource({
  id: 'main',
  name: 'Main library',
  global: 'Hypixel',
  repo: 'not-kathund/not-kathund',
  defaultTag: 'master',
  defaultClass: 'Client',
  branchFilter: branch => !branchBlacklistRegex.test(branch),
  tagFilter: tag => semver.gte(tag, '3.0.0'),
});



